.burgerIngredients {
    height: 290px;
    width: 100%;
    overflow: auto;

}
.burgerIngredients p {
    text-align: center;
}
.ingredientsBlock {
    background-color: #f4b46c;
    margin: 30px auto 0;
    display: table;
    max-width: 200px;
    width: 100%;
    border-radius: 3px;
    padding: 20px;
    text-align: center;

}
.ingredientsBlock p {
    padding: 0;
    margin: 0;
    font-weight: bold;
    font-size: 18px;
    text-transform: uppercase;
}
.ingrBtns {
    margin: 0 0 20px;
    display: flex;
    justify-content: space-between;
}
.ingrBtn {
    border: 0;
    background-color: #c17116;
    color: white;
    padding: 5px;
    cursor: pointer;
    display: table;
    width: 95px;
}
.topSide {
    background-image: url(../components/assets/top.jpg);
    background-size: 300px;
    height: 110px;
    width: 100%;
    display: table;
    background-repeat: no-repeat;
    background-position: center;

}
